import {  useParams } from 'react-router-dom';

const useApi =  (apiFunc:any) => {
  const params = useParams();

  const request = async (...args:any) => {
    try {
      const user_id = localStorage.getItem('user_account_id');
      if(!args[0].includes('permissions'))
      args[0] = `${params.account_name? params.account_name.toString():user_id}_${args[0]}`
      
      const ans =  await apiFunc(...args);
      return ans
    } catch (err:any) {
      return err.toString() || "Unexpected Error!"
 
    }
  };

  return {
    request
  };
};
export default useApi