import { HeadCell } from "types";

export const create_header_cell = (field:string):  HeadCell  => {
    return    {
        field,
        id: field,
        numeric: false,
        label: field.toUpperCase(),
        align: 'center'
    }
}
const create_header = (fields_header:string[]):  HeadCell[]  => 
    fields_header.map((field:string)=>create_header_cell(field))

export const headCellsReport:  HeadCell[] =create_header(['account','report type', 'download'])
export const headCellsAccount:  HeadCell[] =create_header(['name','type', 'bandwidth', 'accounts', 'networks', 'devices', 'users'])
export const headCellsAccountUser:  HeadCell[] =create_header(['name',' role', 'username', 'phone', 'email'])
export const headCellsNetwork:  HeadCell[] =create_header(['owner', 'address', 'rate limit', 'holistic'])
export const headCellsDevice:  HeadCell[] =create_header(['name','mode', 'xflow ip', 'peer ip'])
export const  headCellsAttack = create_header(['destination ip', 'destination ports', 'source ip', 'source ports','protocol', 'tcp flags','packet length'])
export const  headCellsFlow = create_header(['destination ip', 'destination ports', 'source ip', 'source ports','protocol', 'tcp flags','packet length','status'])
export const headCellsBlackhole =  create_header(['address', 'status', 'devices', 'start time', 'end time'])
export const  headCellsFlow2 = create_header([' ', ' ', ' ',' ',' '])
export const  headCellsInnerFlow = create_header(['device name', 'flowsec ip', 'status','bps','pps'])

export const  network_dash = ['address']
export const  device_dash = ['client_flowspec']
export const  attack_dash = ['ip', 'protocol', 'ports']
export const headCellsNetwork_dashboard:  HeadCell[] =create_header(network_dash)
