import {
    array_obj_to_obj_with_key,
    insert,
    map_on_object,
    pydict
} from 'utils';
import {
    FormControlPropsDropLIst,
    icmp_code_types,
    icmp_type_types,
    tcp_flags_types,
    TypeForm
} from 'types';


const is_number = (char: string): boolean => char >= '0' && char <= '9';

export const input_changed_handler = (
    new_value: string,
    inputIdentifier: string,
    updatedForm: any,
    recommend_values = false,
    index = -1, values=false,rate_limit_bool=false,model=''
) => {
        let props_temp =['0']
        try {
                
                 props_temp = inputIdentifier.split('');
        } catch (error) {
                        console.log(error, new_value);
                        
                //   props_temp =props_temp
        }
        // const  props_temp = inputIdentifier.split('');
        
    const props: string[] = [];
    for (let i = 0; i < props_temp.length; i += 1) {
        let temp = props_temp[i];
        while (i + 1 < props_temp.length && is_number(props_temp[i]) && is_number(props_temp[i + 1])) {
            temp += props_temp[i + 1];
            i += 1;
        }
        props.push(temp);
    }

    const lastKey = props.pop() as string;
    const nestedObj = props.reduce((a: any, prop: string) => {
        // const nestedObj = props.reduce((a: FormControlObject | FormField[] | any, prop: string) => {
        if (prop === '_') return a.values;
        return prop === '.' ? a.form_elements : a[parseInt(prop, 10)];
    }, updatedForm);
    // if(nestedObj[lastKey].hasOwnProperty('values'))
    // if(inputIdentifier.includes('_'))
    // console.log(nestedObj[lastKey]);
    //     if(lastKey==='0')
    //     console.log(new_value, nestedObj);
//     try {
//         if (rate_limit.index > -1) nestedObj[lastKey].values[rate_limit.index][rate_limit.key] = new_value;
//         else if (recommend_values) nestedObj[lastKey].recommend_values = new_value;
//         else nestedObj[lastKey].value_cur = new_value;
//     } catch (error) {
//         // if (rate_limit.index > -1) nestedObj[lastKey].values[rate_limit.index][rate_limit.key] = new_value
//         if (recommend_values) nestedObj[lastKey].recommend_values = new_value;
//         else nestedObj[lastKey].value_cur = new_value;
//     }
        if (index > -1) {
            if(nestedObj[lastKey].values)
            nestedObj[lastKey].values[index]= new_value
            else nestedObj[lastKey].ports[index]= new_value
        }
        else if(rate_limit_bool ) nestedObj[lastKey].ports = new_value

        else if (recommend_values) nestedObj[lastKey].recommend_values = new_value
        else if (values) nestedObj[lastKey].values = new_value
        else nestedObj[lastKey].value_cur = new_value
    
        
        if (model === 'attacks') {
            const protocol = array_obj_to_obj_with_key(updatedForm, 'protocol', 'placeholder');
            const tcp_flags = array_obj_to_obj_with_key(updatedForm, 'tcp_flags', 'placeholder');
            const icmp_code = array_obj_to_obj_with_key(updatedForm, 'icmp_code', 'placeholder');
            // gilad need to fix here
            if (updatedForm[0].value_cur !== 'rate-limit' && updatedForm[1].placeholder === 'rate_limit_bps') {
                updatedForm.splice(1, 1);
                let index_form = updatedForm.length;
                while (--index_form > 1)
                    if (updatedForm[index_form].identifier !== undefined)
                        updatedForm[index_form].identifier =
                            updatedForm[index_form].identifier.length > 1
                                ? `${index_form.toString()}${updatedForm[index_form].identifier.slice(1)}`
                                : index_form.toString();
                    else if (updatedForm[index_form].key !== undefined)
                        updatedForm[index_form].key =
                            updatedForm[index_form].key.length > 1
                                ? `${index_form.toString()}${updatedForm[index_form].key.slice(1)}`
                                : index_form.toString();
            }
            if (protocol.value_cur !== 'icmp' && icmp_code !== undefined) {
                updatedForm.splice(-2);
            }
            if (protocol.value_cur !== 'tcp' && tcp_flags !== undefined) {
                updatedForm.pop();
            }

            if (updatedForm[0].value_cur === 'rate-limit' && updatedForm[1].placeholder !== 'rate_limit_bps') {
                const tmp = map_on_object([{ type: 'FormControlProps', values: ['rate_limit_bps', TypeForm.NUMBER] }]);
                updatedForm = insert(updatedForm, 1, tmp[0]);
            }
            if (protocol.value_cur === 'tcp' && tcp_flags === undefined) {
                const tmp = new FormControlPropsDropLIst(
                    'FormControlPropsDropLIst',
                    'tcp_flags',
                    tcp_flags_types,
                    updatedForm.length.toString(),
                    [],
                    TypeForm.MULTI_DROPLIST
                );
                updatedForm.push(tmp);
            }

            if (protocol.value_cur === 'icmp' && icmp_code === undefined) {
                const tmp = [
                    new FormControlPropsDropLIst('FormControlPropsDropLIst', 'icmp_code', icmp_code_types, updatedForm.length.toString()),
                    new FormControlPropsDropLIst(
                        'FormControlPropsDropLIst',
                        'icmp_type',
                        icmp_type_types,
                        (updatedForm.length + 1).toString()
                    )
                ];

                updatedForm = updatedForm.concat(tmp);
            }
        }
    return updatedForm;
    // setFields(updatedForm);
};

export const merge_deep = (target: any, source: any, idef: any, fields: any, set_fields: any) => {
    if (target && source && idef) {
        Object.keys(source).forEach((key) => {

            if (target.hasOwnProperty(key)) {
                if (typeof idef === 'string' && idef.includes('_')) {
                    //     if (key === 'relative_value'||key === 'unit' || key ===  'enabled')
                    //     console.log(key, target, source);

                    if (key === 'unit' || key === 'enabled')
                        // set_fields(input_changed_handler(target[key], `${idef.slice(0, -1)}0`, fields));
                        fields = input_changed_handler(target[key], `${idef.slice(0, -1)}0`, fields);
                    // if (key === 'relative_value')
                    // console.log( source, target);

                    if (key === 'relative_value' || key === 'threshold')
                        // set_fields(input_changed_handler(target[key], `${idef.slice(0, -1)}1`, fields));
                        fields = input_changed_handler(target[key], `${idef.slice(0, -1)}1`, fields);
                    // else
                    // console.log(target, idef,key, source);
                    // const temp_idef = {[key]:idef}
                    //       mergeDeep(target, source, idef);
                } else if (key === 'rate_limit') {

                        const temp= [...fields]
                        const index = parseInt(idef[key], 10)
                        temp[index].value_cur =target[key].enabled
                const traffic_series = pydict(target, `${key}.ports`, []);
                        // console.log(traffic_series, source, target);
                        
                        temp[index].ports =traffic_series
                        // set_fields(temp)
                        fields = temp

                //     const a = input_changed_handler(target[key], idef[key], fields);
                //     console.log(a);
                //     console.log(target);

                //     set_fields(target);
                } else if (key === 'asns') {
                    // set_fields(input_changed_handler(target[key], idef[key], fields));
                    fields = input_changed_handler(target[key], idef[key], fields);
                } else if (typeof source[key] === 'object') {
                    merge_deep(target[key], source[key], idef[key], fields, set_fields);
                } else if (typeof target[key] === 'object') {
                    // set_fields(input_changed_handler(target[key].value_cur, idef[key], fields));
                    fields = input_changed_handler(target[key].value_cur, idef[key], fields);
                    if (target[key].recommend_values !== undefined)
                        // set_fields(input_changed_handler(target[key].recommend_values, idef[key], fields, true));
                        fields = input_changed_handler(target[key].recommend_values, idef[key], fields, true);
                } else fields = input_changed_handler(target[key], idef[key], fields)
                // } else set_fields(input_changed_handler(target[key], idef[key], fields));
            }
            //   else console.log(key);
        });
    }

    return fields;
};
