import {  DashboardTabEnum, NavigationEnum } from 'types';
import {  normliaze_bps, pydict , normal_pps} from '.';

type Totals = {
    bps: number;
    link_capacity: number;
};
const devide = (totals: Totals, key :'bps' | 'link_capacity'='bps', network = false) => {
    try {
        if (network) return totals[key] > 0 ? parseFloat((totals[key] / 1024 / 1024 / totals.link_capacity).toFixed(2)) : 0;
        return totals[key] > 0 ? parseFloat((totals[key] / 1024 / totals.link_capacity).toFixed(2)) : 0;
    } catch (error) {
        return 0.0;
    }
};
interface IDashboradTotals {
    attacks: any;
    flows: any;
    traffic: any;
    max_time: string;
    under_attack: boolean;
    link_capacity: number;
}
export enum TotalsPoints {
    FLOWS = 'flows',
    ATTACKS = 'attacks',
    TRAFFIC = 'traffic',
}
export const default_totals2:IDashboradTotals = {
    link_capacity:51200000,
    traffic:{},
     max_time:'',
      under_attack:false,
      attacks:{}, 
      flows:{} 
};

export class DashboradTotals implements IDashboradTotals {
    attacks: any;
    flows: any;
    traffic: any;
    max_time: string;
    under_attack: boolean;
    link_capacity: number;

    constructor(totals: DashboradTotals) {
        
        const { traffic, max_time, under_attack,attacks, flows , link_capacity} ={...default_totals2,...totals};
        this.attacks = attacks;
        this.flows = flows;
        this.traffic = traffic;
        this.max_time = max_time;
        this.under_attack = under_attack;
        this.link_capacity = link_capacity;
    }

    get_total_for_linechart(model:DashboardTabEnum){
        return [ this.attacks, model === DashboardTabEnum.NETWORKS_TABLE?this.traffic:this.flows]
    }
    get_total_for_info(model:DashboardTabEnum){
        const first =  this.get_total_by_key(model === DashboardTabEnum.NETWORKS_TABLE?TotalsPoints.TRAFFIC:TotalsPoints.FLOWS)
        const attacks = this.get_total_by_key(TotalsPoints.ATTACKS)
        return [first, attacks]
    }
    get_total_by_key(key:TotalsPoints){
        const {bps, pps} = pydict(this,`${key}.${this.max_time}`, {bps:0, pps:0})
        return {
            bps: normliaze_bps(bps),
            pps: normal_pps(pps),
            gauge: devide({ bps, link_capacity: this.link_capacity } as Totals),
            title: key,
             text: `${key}_info`

            }

    }
}

export const default_totals = {
    gauge: 0,
    bps: { value: 0, text: 'MB' },
    pps: 0,
    account_link_capacity: 51200000,
    outgoing_flows_total_mbps: 0,
    outgoing_flows_total_pps: 0
};


export const make_total_with_totals_api = (totals:any, model:string, lead_key:string)=>{
    const make_total=(traffic_dict:any)=>{
        let [bps, pps]= [0, 0]
        
        if(traffic_dict[lead_key]){
        bps= traffic_dict[lead_key].bps
        pps= traffic_dict[lead_key].pps
    }

 
        return {
            bps: normliaze_bps(bps),
            pps: normal_pps(pps),
            // gauge: devide({ bps, account_link_capacity: 51200000 } as Totals,ATTACK_FIELDS.BPS)


            }
    }
    const attacks = { title: 'attacks', text: 'attack_info', ...make_total(pydict(totals,'attacks',{})) }
    switch (model){
        case NavigationEnum.ATTACKS_TABLE:
        case NavigationEnum.DEVICES_TABLE:

            return [
                { title: 'flows', text: 'flows_info', ...make_total(pydict(totals,'flows',{})) },
                attacks
            ]
 
        default:
            return [
                { title: 'traffic', text: 'traffic_info', ...make_total(pydict(totals,'traffic',{}))},
                attacks
            ]
    }

}

