import config from "config";

 
 
export const get_normliaze_func = (lead_num: number, bps:undefined|boolean) => {
    let func_normal:Function;
    const parseFixed = (n:number) => parseFloat(n.toFixed(2))
    if(bps){
       if(lead_num>3)
        func_normal= (n: number) => {
            return {value: parseFixed(n /1024), text:'Gb'}         }
        else   func_normal= (n: number) => {
            return {value:parseFixed(n ), text:'Mb'}
       
        }

    }
    // if(bps){
    //     if(lead_num>9)
    //     func_normal = (n: number) => {
    //         return {value:  parseFixed(n * 8/60/ 1024/1024/1024), text:'GB'}
    //     }
    //     else if(lead_num>6)
    //     func_normal= (n: number) => {
    //         return {value: parseFixed(n * 8/60/1024/1024), text:'MB'}         }
    //     else 
    //     // if(lead_num>3)
    //     // func_normal= (n: number) => {
    //     //     return {value: parseFixed(n /1024), text:'GB'}         }
    //     // else
    //     func_normal= (n: number) => {
    //         // return {value:parseFixed(n ), text:'KB'}
    //         // return {value:parseFixed(n ), text:'MB'}
    //         return {value:parseFixed(n *8 / 60/1024), text:'KB'}
       
    //     }

    // }
    else if(bps===undefined&&lead_num>7)
        func_normal = (n: number) => {
            return {value:parseFixed(n /  1000000).toFixed(0),text:'M'};

  
        }
        else if(lead_num>4)
        func_normal= (n: number) => {
            return {value:(n / 1000).toFixed(0), text:'K'}
        }
        else
        func_normal= (n: number) => {
       
            return {value:n, text:''}

        }
    return func_normal

}

export const  normliaze_bps = (n:number) => {

    const func = get_normliaze_func(n.toFixed(0).length, true)
    return func(n)
     
   
}
export const normliaze_pps = (pps: number) => {
    if (pps > 1000) {
        // if (pps < 1000 * 1000) return {value:(pps ).toFixed(0).toString(), text:'K'};
        if (pps < 1000 * 1000) return {value:(pps / 1000).toFixed(0).toString(), text:'K'};
        return {value:(pps / 100000).toFixed(0).toString(),text:'M'};
    }
    return {value:pps.toString(),text:''};
}
export const normal_pps = (n: number) => {
    const func = get_normliaze_func(n.toFixed(2).toString().length, false)

    // console.log(n, ' pps');

    
    let ans = func(n)
    ans  = `${ans.value}${ans.text!==''?`|${ans.text}`:''}`
    return ans
 
};
export const capitalize_array = (st: string[]) => (
    st.map((s: string) =>
      (s.length > 0 && s[0].toUpperCase() + s.slice(1)) || " ").join(' ')
  )


  


  export const create_min_max_for_chart = (number_series: any[], bool_series:boolean[],is_bps?:boolean)=>{
    let max=12 ;
    let min=0 ;

    const numbers_points:any=number_series.filter((s:any,index:number)=> bool_series[index]).map((s:any) =>s.data).flat(2).map((n:any)=>parseInt(n,10)).filter((n:any)=>n>10)
    if(numbers_points.length){
     max = Math.max(...numbers_points)
     min = Math.min(...numbers_points)
    }
    
    const [min_len, max_len] = [min.toString().length, max.toString().length]
    // console.log(min_len, max_len, bps);
    const lead_num = min_len<3 ||min_len+3<max_len?max_len:min_len
    const func_normal:Function=get_normliaze_func(lead_num, is_bps)
    
    const seris_to_set:any[] = number_series.map((s:any)=>s.data).map((s:number[])=>   s.map((x:number)=>func_normal(x))).map((x:any, index:number)=>{ 
        
        // return {data:  x.map((a:any)=>a.value) , name:number_series[index].name, text:x[x.length-1].text}})
        return {data: bool_series[index]?x.map((a:any)=>a.value):Array(config.len_traffic).fill(0), name:number_series[index].name, text:bool_series[index]?x[x.length-1].text:'no'}})



        max = Math.max(...seris_to_set.filter((s:any,index:number)=> bool_series[index]).map((s:any) =>s.data).flat(2).map((n:any)=>parseInt(n,10)))
        let max_str:any= max.toString()
        const attack_pps = Array(max_str.length-1).fill(0).join('');
        max_str = parseInt(max_str.slice(0,1),10)
        // console.log(max_str, attack_pps);
        if(max_str>7)
            max_str='12'
        else if(max_str>3)
            max_str='8'
        else 
            max_str='4'

        const min_max= {  min:0,            
            max:parseInt(`${max_str}${attack_pps}`,10)
            ,}  
                
    return {seris_to_set, min_max}
    }