import { Dispatch } from "redux";
import jwtDecode from 'jwt-decode';

import axios, { AxiosResponse } from 'axios';
import { SET_LOADING, SNACKBAR_OPEN } from "store/state/type_actions";
import { pydict } from "./transformation";
import { KeyedObject } from "types";
import moment from "moment";


export const API = axios.create({
    baseURL: '',
    headers: {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET,PUT,POST,DELETE,PATCH,OPTIONS',
        // "Access-Control-Allow-Origin": true,
        'Access-Control-Allow-Credentials': true,
        'content-Type': 'application/json',
        withCredentials: false
        // crossDomain: true,
    }
});
export const refresh_token: (st: string) => boolean = (serviceToken) => {
    if (!serviceToken) {
        return false;
    }

    const decoded: KeyedObject = jwtDecode(serviceToken);
    const halfAnHourHead = moment().subtract(30, 'minutes').toDate().getTime()/1000;
    
    return decoded.exp > halfAnHourHead;
};

API.interceptors.request.use(
    (config) => {
        config.baseURL += '/api2'

        const token = localStorage.getItem('token');
        if (token) {
          
            config.headers['refresh_token'] = refresh_token(token);

            config.headers['Authorization'] = `Bearer ${token}`;

        }
        return config;

    },
    (error) => {
        return Promise.reject(error);
    }
);

API.interceptors.response.use(
    (response)=> {
        const token = pydict(response.headers, 'refresh_token')
        if(token)localStorage.setItem('token', token);
        
        return response;
    },
 
);
// const errorApi = (error: { response: { data?: { msg: string } }; message: string }):string =>
//     error.response && error.response.data ? error.response.data.msg : error.message;
const error_api = (error_res: any, url: string, method: string, body = {}) => {

    const error_msg =pydict(error_res, 'response.data.error' ,error_res.message)
    console.error(`${method} ${url} error message: ${error_msg}, body: ${JSON.stringify(body)}`);
    return error_msg;
};
export const change_state = (method: string, url: string) => async (dispatch:Dispatch<any>)  => {

    let msg_url = url&& url.length>0? url.split('/').reverse().join(' ').trim():'load'
    if(['add','edit'].includes(url))
    msg_url = msg_url.slice(0, -1)

  switch (method) {
    case 'start':
      dispatch({
        type: SET_LOADING,
        loading: true,
    });
        break;
    case 'end':
      dispatch({
        type: SET_LOADING,
        loading: false,
    });
        break;
    case 'end_error':
      dispatch({
        type: SNACKBAR_OPEN,
        open: true,
        alertSeverity:'error',
        variant:'alert',

        message:`faild to ${msg_url}`
    });
    break
    case 'end_normal':
      dispatch({
        type: SNACKBAR_OPEN,
        open: true,
        alertSeverity:'success',
        variant:'default',

        message:`success to ${msg_url}`
    });
        break;
   
    
}
}
// return


// export const send_api = (method: string, url: string, body = {}) => async (dispatch:Dispatch)  => {

const send_api = async (method: string, url: string,dispatch:Dispatch<any>, body = {}) => {
    let response: AxiosResponse<any>;
    try {

        url = url.replace('_','/')
      dispatch(change_state('start', url))
        
      switch (method) {
        case 'post':
                response = await API.post(url, body);
                break;
            case 'get':
                response = await API.get(url, body);
                break;
            case 'put':
                response = await API.put(url, body);
                break;

            case 'delete':
                response = await API.delete(url);
                break;
            default:
                response = await API.patch(url);
        }
        //    data.success&&
        dispatch(change_state('end', url))
        const data = response.data;
        
        if(pydict(response,'data.success',true)){
            url = url.split('/').filter((st:string)=>/^[a-zA-Z()]+$/.test(st)).join('')
            if(!url.includes('permissions'))
            dispatch(change_state('end_normal', url))
        return [true,data.success? data.data:data];
    }
        // else {
  
             dispatch(change_state('end_error', url))

            return[false, data.error]
        // }
    } catch (error: any) {
        console.log(error);
        
        dispatch(change_state('end', url))
        const error_msg:string = error_api(error, url, method, body)
        if (error.message.toString() ==='Request failed with status code 401')
        {
            
            dispatch(change_state('end_error', 'auth'))
            return[false, 'auth']

        }
        
        dispatch(change_state('end_error', error_msg))

        return [false, error_msg];
    }
    
};
export const objApi = {
    post: async (url: string, body = {}, dispatch:Dispatch) => {
        // const bb = {...body,     "status": "new"}
        // return  send_api('post', url,dispatch, bb );
        return  send_api('post', url,dispatch, body );
    },
    get: async (url: string, dispatch:Dispatch,config={}) => {
        return send_api('get', url,  dispatch, config);
    },
    put: async (url: string, body = {}, dispatch:Dispatch) => {
        return send_api('put', url, dispatch, body);
    },
    delete: async (url: string, dispatch:Dispatch) => {
        return send_api('delete', url, dispatch);
    }
};
