import {

  FormControlProps, FormControlPropsDropLIst, TypeForm, FormControlObject,  ToMap, FormField,
  FormControlPropsCheckBox, FormControlPropsRadioButtonWithInput, FormControlPropsSwitchAnimate, FormControlPropsChips,FormControlPropsThreshold, FormControlRateLimit
} from "types"


export const map_on_object = (to_map: ToMap[], key=' '): FormField[] => to_map.map((obj:ToMap, idx)=>{
  
  // key = key.slice(0, -1) +  idx.toString()
  key = key.slice(0, -1) 

  while(key.length && key.charAt(key.length-1 ) >= '0' && key.charAt(key.length-1 ) <= '9')
  key = key.slice(0, -1) 
  key +=  idx.toString()
  if(obj.type === 'FormControlProps')
 { const autoComplete = obj.values.length>2 ?obj.values[2]:undefined
  return new FormControlProps(obj.type, obj.values[0], obj.values[1], key, autoComplete)
}  
if(obj.type === 'FormControlPropsDropLIst')
  return new FormControlPropsDropLIst(obj.type, obj.values[0], obj.values[1], key, '', obj.values.length>2?obj.values[2]: TypeForm.DROPLIST)
if(obj.type === 'FormControlPropsThreshold')
  return new FormControlPropsThreshold(obj.type, obj.values[0], key)

  if(obj.type === 'FormControlPropsRadioButtonWithInput')
  return new FormControlPropsRadioButtonWithInput(obj.type, obj.values[0], obj.values[1],`${key}_0`,
   new FormControlProps(obj.type, obj.values[0], obj.values[2], `${key}_1`,))

  if(obj.type === 'FormControlPropsSwitchAnimate')
  return new FormControlPropsSwitchAnimate(obj.type, obj.values[0], `${key}_0`,
   new FormControlProps(obj.type, obj.values[0], obj.values[1], `${key}_1`,undefined, undefined, obj.values[2]))

   if(obj.type === 'FormControlPropsCheckBox')
  return new FormControlPropsCheckBox(obj.type, obj.values[0], key, false, obj.values[1])

   if(obj.type === 'FormControlPropsChips')
  return new FormControlPropsChips(obj.type, obj.values[0], key)

   if(obj.type === 'FormControlPropsRateLimit'){

    return new FormControlRateLimit(obj.type, obj.values[0], `${key}`)
}
   if(obj.type === 'FormControlObjectOpen'){
    
    return new FormControlObject(obj.type, obj.values[0], map_on_object(obj.values[1] as ToMap[], `${key}. `), key, TypeForm.OPEN)

}
  return new FormControlObject(obj.type, obj.values[0], map_on_object(obj.values[1] as ToMap[], `${key}. `), key)
})
export const object_to_values = (fields: FormField[]): any =>Object.assign({}, ...fields.map((obj:FormField|any)=>{
  
  if(['FormControlObject' ,'FormControlObjectOpen'].includes(obj.type1))
  return {[obj.placeholder]: object_to_values(obj.form_elements)}
  
  if(['FormControlPropsRadioButtonWithInput', 'FormControlPropsSwitchAnimate'].includes(obj.type1))
  return {[obj.placeholder]: { [typeof obj.values[0].value_cur !=='boolean' &&obj.placeholder!=='blackhole' ? 'unit': 'enabled']: obj.values[0].value_cur, [typeof obj.values[0].value_cur !=='boolean'&&obj.placeholder!=='blackhole'  ?'relative_value': 'threshold']: parseInt(obj.values[1].value_cur, 10)}}
  
  
  if(['FormControlPropsRateLimit'].includes(obj.type1)){
  //   try {
  //     const a =  {[obj.placeholder]: { 'enabled': obj.value_cur, ports:obj.ports}}
  //     return a
  //   } catch (error) {
  // console.log(error, obj);

  //   }
    const ports = obj.ports.filter((item:any)=> Object.keys(item).some((key)=>typeof item[key] ==='number'))
    return {[obj.placeholder]: { 'enabled': obj.value_cur, ports }}
    
}
  return {[obj.placeholder]: obj.type === TypeForm.NUMBER && obj.value_cur !==''? parseInt(obj.value_cur, 10):obj.value_cur}
  
}))
export const object_to_idefinter = (fields: FormField[]): any =>Object.assign({}, ...fields.map((obj:FormField|any)=>{
  
  if(['FormControlObject' ,'FormControlObjectOpen'].includes(obj.type1))
  return {[obj.placeholder]: object_to_idefinter(obj.form_elements)}
  
  // if(['FormControlPropsRadioButtonWithInput', 'FormControlPropsSwitchAnimate'].includes(obj.type1))
  // return {[obj.placeholder]: { [typeof obj.values[0].value_cur !=='boolean' &&obj.placeholder!=='blackhole'? 'unit': 'enabled']: obj.values[0].value_cur, [typeof obj.values[0].value_cur !=='boolean' ?'value': 'threshold']: parseInt(obj.values[1].value_cur, 10)}}
 
  return {[obj.placeholder]:obj.identifier}
  
}))