import config from "config";
// import {  intersection } from "lodash";
import { NavigationEnum, ATTACK_FIELDS, DashboardTabEnum } from "types";
import { Dashborad } from "types/dashboard/Dashboard";
import {  create_time_arr_moment } from "types/mock/traffic";
import {  array_to_index_with_key, pydict } from "utils/transformation";
import { get_ids_to_sum } from "./help_function";
import moment  from 'moment'

export type Traffic = {
    traffic_series: any;
    traffic_bps: number[];
    traffic_pps: number[];
};

const add_traffic_series = (props: Traffic,time_arr:any):any => {
    const { traffic_series, traffic_bps, traffic_pps } = props;
    try {
        Object.keys(traffic_series).forEach((time: string) => {
            let index = array_to_index_with_key(time_arr, time);
            if (index === -1) {
                index = array_to_index_with_key(time_arr, time.replace(' ', 'T'));
            }

            if (index > -1) {
                traffic_bps[index] += pydict(traffic_series, `${time}.bps`, 0);
                traffic_pps[index] += pydict(traffic_series, `${time}.pps`, 0);
            }
        });
    } catch (error) {
        console.log(error);
    }
    return { traffic_series, traffic_bps, traffic_pps };
};

export const make_traffic = (ids_to_calculate:string[], objs:any, traffic_key:string, time_arr:string[])=>{
    let points_pps = Array( config.len_traffic).fill(0);
    let points_bps = Array( config.len_traffic).fill(0);

    ids_to_calculate.forEach((obj_id: string) => {
        const traffic_series = pydict( objs, `${obj_id}.${traffic_key}`, {});
        // const traffic_series = pydict(array_obj_to_obj(objs, obj_id), traffic_key, {});

        const ans = add_traffic_series({ traffic_series, traffic_bps: points_bps, traffic_pps: points_pps },time_arr);
        points_bps = ans.traffic_bps;
        points_pps = ans.traffic_pps;
    });
    return { points_bps,points_pps }
}
export const calculate_chart = (dashboard_class:Dashborad, model:any,  context :any, 
    dashboard_data :any, 
   chart_line_name :any, 
   global_attack :any
    
    ) => {

const len_traffic = config.len_traffic;

const [total_line_key1,total_line_key2] = dashboard_class.model_name === DashboardTabEnum.NETWORKS_TABLE ?['attacks', 'traffic']:['attacks', 'flows']
    const [network_totala, device_totala] =  dashboard_class.totals.get_total_for_linechart(dashboard_class.model_name)
    const lead_time = dashboard_class.totals.max_time
        
    
    const time_arr = create_time_arr_moment(moment(lead_time))
    const indexs_to_show = pydict(dashboard_data, 'totals.traffic_simples_points', []).map((p:string)=> time_arr.indexOf(p))
    .filter((p:number)=> p!==-1);
    
    const rows_merge = dashboard_class.merege_model()
    const merge_attack_model =dashboard_class.merege_attacks()   
    const merge_flows_model = dashboard_class.flows  

    const { first, second } = {
        ...get_ids_to_sum(
            model,
            context,
            rows_merge ,
        )
    };
    let traffic:any;
    let attacks_points:any;
    if (model === NavigationEnum.NETWORKS_TABLE) {
        
        traffic = make_traffic(first, rows_merge, ATTACK_FIELDS.TRAFFIC__POINTS, time_arr)
        attacks_points = make_traffic(second, merge_attack_model, ATTACK_FIELDS.STATICTICS, time_arr)
 
    }
    if (model === NavigationEnum.DEVICES_TABLE) {
         traffic = make_traffic(first, merge_flows_model, ATTACK_FIELDS.STATICTICS, time_arr)
 
        attacks_points = make_traffic(second, merge_attack_model, ATTACK_FIELDS.STATICTICS, time_arr)
 

    }
    if (model === NavigationEnum.ATTACKS_TABLE) {
        traffic = make_traffic( first , merge_flows_model, ATTACK_FIELDS.STATICTICS, time_arr)
        
        
         attacks_points = make_traffic(second, merge_attack_model, ATTACK_FIELDS.STATICTICS, time_arr)

 
    }
    const make_nums = (obj:any, key:string)=>{
        
        let ans =  Object.keys(obj).filter((d:string)=> time_arr.includes(d)).map((d:string)=> pydict(obj[d],key,0))
        
        if(ans.length<len_traffic)
        ans = [...Array(len_traffic-ans.length).fill(0), ...ans]
        if(ans.length>len_traffic)
        ans = ans.slice(0,len_traffic)

        return ans 
    }
    

    
    return {
        sa: [
            { name: chart_line_name, data: traffic.points_bps },
            { name: global_attack, data: attacks_points.points_bps },
            { name: `TOTALS ${total_line_key2.toLocaleUpperCase()}`, data: make_nums(device_totala,'bps') },
            { name: `TOTALS ${total_line_key1.toLocaleUpperCase()}`, data: make_nums(network_totala, 'bps') },
        ],
        sb: [
            { name: chart_line_name, data: traffic.points_pps },
            { name: global_attack, data: attacks_points.points_pps },
            { name: `TOTALS ${total_line_key2.toLocaleUpperCase()}`, data: make_nums(device_totala,'pps') },
            { name: `TOTALS ${total_line_key1.toLocaleUpperCase()}`, data: make_nums(network_totala, 'pps') },
        ],
       indexs_to_show 

    };
};