import { LINK_CAPACITY_UNIT, Role, Vendor, APPLY_ACTION, APPLY_OPTION } from '.';


export enum Account_Enum {
  name='name',type='type',email='email',phone='phone',website='website',address='address',city='city',country='country',state='state',zip='zip'
  ,rate_limit__enabled ='rate_limit.enabled'}

export enum Network_Enum {
  owner='owner',address='address',holistic='holistic',rate_limit__enabled ='rate_limit.enabled'}
export enum Device_Enum {
  apply_action='apply_action',apply_option='apply_option',max_allowed_flows='max_allowed_flows',model='model',name='name'
}
export enum User_Enum {
  role='role',username='username',phone='phone',email_address='email_address',name='name',
}




export interface LoginForm {
  username: string;
  password: string;
};
// // 
export interface DeviceForm {
  id?: string;
  name: string;
  vendor: Vendor;
  apply_action: APPLY_ACTION;
  moderated: APPLY_OPTION;

};


export interface NetworkForm {
  id?: string;
  ip: string;
  mask: number;
  rate_limit: any;
  holistic: boolean;


};



export interface AccountUserForm {
  id?: string;
  role: Role;
  name: string;
  username: string;
  password: string;
  phone?: string;
  email?: string;
};

export interface AccountForm {
  id?: string;
  name: string;
  type: string;
  link_capacity_unit: LINK_CAPACITY_UNIT;
  account_bandwidth_mbps: number;
  asns: number[];
  blackhole: boolean;
  community_attacks_emails: boolean;
  rate_limit: any;

  blackhole_threshold: number;
  address: string;
  phone: number;
  state: string;
  website: string;
  zip: string;
  city: string;
  country: string;
  email: string;
};

