import { lazy } from 'react';

// project imports
import MainLayout from 'common/layout/MainLayout';
import Loadable from 'ui-component/pages/Loadable';
import AuthGuard from 'common/route-guard/AuthGuard';

// dashboard routing
const DashboardDefault = Loadable(lazy(() => import('views/dashboard')));


// application - user social & account profile routing
const AppUserAccountProfile1 = Loadable(lazy(() => import('views/secunity-ui/profile')));
const TableList = Loadable(lazy(() => import('views/secunity-ui/Table/table_model/TableBase')));



const ContainerForm = Loadable(lazy(() => import('views/secunity-ui/Form/ContainerForm')));




// chart routing




// ==============================|| MAIN ROUTING ||============================== //

const MainRoutes = {
    path: '/',
    element: (
        <AuthGuard>
            <MainLayout />
        </AuthGuard>
    ),
    children: [




        {
            path: '/profile/:model_name/:id',
            element: <AppUserAccountProfile1 />
        },




        {
            path: '/table/:model_name/:page',
            element: <TableList  />
        },

        {
            path: '/table/:model_name',
            element: <TableList  />
        },


        {
            path: '/forms/:model_name/:id',
            // path: '/forms/layouts/action-bar',
            element: <ContainerForm />
        },

        {
            path: '/dashboard/:model_name',
            element: <DashboardDefault />
        },
        {
            path: '/dashboard/:model_name/p/:page',
            element: <DashboardDefault />
        },
        {
            path: '/dashboard/:model_name/:id',
            element: <DashboardDefault />
        },
        {
            path: '/dashboard/:model_name/p/:page/:id',
            element: <DashboardDefault />
        },
        {
            path: '/dashboard',
            element: <DashboardDefault />
        },
        {
            path: '*',
            element: <DashboardDefault />
        },
    ]
};

export default MainRoutes;
