import React, { createContext,  useMemo, useReducer } from 'react';
import { useDispatch } from 'react-redux';

import jwtDecode from 'jwt-decode';

import { LOGIN, LOGOUT, RESET_DASHBOARD, RESET_CONTEXT, SET_PERMISSIONS } from 'store/state/type_actions';
import accountReducer from 'store/template/accountReducer';

import { objApi } from 'utils/api';
import { initialLoginContextProps, KeyedObject, LoginForm, JWTContextType, NavigationEnum } from 'types';

const initialState: initialLoginContextProps = {
    isLoggedIn: false,
    permissions: {},
};

export const verifyToken: (st: string) => boolean = (serviceToken) => {
    if (!serviceToken) {
        return false;
    }

    const decoded: KeyedObject = jwtDecode(serviceToken);
 
    return decoded.exp > Date.now() / 1000;
};

const JWTContext = createContext<JWTContextType | null>(null);

export const JWTProvider = ({ children }: { children: React.ReactElement }) => {
    const [state, dispatch] = useReducer(accountReducer, initialState);
    // localStorage.setItem('token', aa);
    const dispatch_cart = useDispatch();


    const login = async (email: string, password: string) => {
        
        try {
            const login_form: LoginForm = { username: email, password };
            // const [response, data] = await objApi.post(`/api2/${NavigationEnum.LOGIN}`, login_form, dispatch_cart);
            const [response, data] = await objApi.post(`/${NavigationEnum.LOGIN}`, login_form, dispatch_cart);

            if (response && data !== undefined && !data.error) {
                localStorage.setItem('token', data['access_token']);
                localStorage.setItem('user_id', data['user_id']);
                localStorage.setItem('user_account_id', data['user_account_id']);
                localStorage.setItem('user_account_name', data['user_account_name']);
                localStorage.setItem('user_account_type', data['user_account_type']);
                await get_context_user2(data['user_account_id'])

                dispatch({
                    type: LOGIN,
                    payload: {
                        isLoggedIn: true
                    }
                });
            } else {
                logout();
            }
        } catch (err: any) {
            console.log(err);
        }
    };

    const logout = () => {
        localStorage.clear();
        dispatch_cart({ type: RESET_DASHBOARD });
        dispatch_cart({ type: RESET_CONTEXT });
        dispatch({ type: LOGOUT });

    };
    const get_context_user = async(url:string) => {
        
        const [ans, permissions]= await objApi.post(`/${url}`, {},dispatch_cart);
        if (ans)
       {
        dispatch({ type: SET_PERMISSIONS, payload:{permissions, isLoggedIn:true} });
        }
        // localStorage.clear();
        // dispatch_cart({ type: RESET_CART });
        // dispatch_cart({ type: RESET_CONTEXT });
        // dispatch({ type: LOGOUT });

    };
    const get_context_user2 = async(account_id:string) => {
        const [ans, permissions]= await objApi.post(`/${account_id}/permissions`, {},dispatch_cart);
        if (ans)
       {
            localStorage.setItem('permissions', JSON.stringify(permissions))
        dispatch({ type: SET_PERMISSIONS, payload:{permissions, isLoggedIn:true} });
        }
        // localStorage.clear();
        // dispatch_cart({ type: RESET_CART });
        // dispatch_cart({ type: RESET_CONTEXT });
        // dispatch({ type: LOGOUT });

    };

    useMemo(() => {
            try {
                const token = localStorage.getItem('token');
                    
                if (token && verifyToken(token)) {
                    dispatch({
                        type: LOGIN,
                        payload: {
                            isLoggedIn: true
                        }
                    });
        // const cartItemsData = localStorage.getItem('permissions')
        // if (cartItemsData){
        //     const permissions= JSON.parse(cartItemsData)
        //             dispatch({
        //                 type: SET_PERMISSIONS,
        //                 payload: {
        //                     isLoggedIn: true,
        //                     permissions
        //                 }
        //             });}
                    
                } else {
                    logout();
                }
            } catch (err: any) {
                console.log(err, err.response, err.message);
                logout();

            }

    }, []);

    return <JWTContext.Provider value={{ ...state, login, logout, get_context_user }}>{children}</JWTContext.Provider>;
};

export default JWTContext;
