export const create_time_arr = (end_time:number)=>Array.from(Array(config.len_traffic).keys()).map(i => (new Date( end_time -  1000 * (60 * (i )) ).toISOString().slice(0, -8).replace('T', ' '))).reverse()
export const format_time = "YYYY-MM-DD HH:mm"

export const create_time_arr_moment = (end_time:Moment)=>Array.from(Array(config.len_traffic).keys()).map(i => (  moment(end_time).subtract(i, 'minutes').format(format_time).toString() )).reverse()
