export const sub_timezone_for_time = ( lead_time:any)=>{
    
    const moment_lead_time: Moment = moment().utc(lead_time);
    console.log(lead_time, moment_lead_time);
    const localzone_diff = moment
        .duration(moment_lead_time.diff(moment(lead_time)))
        .asHours()
        .toFixed();
     
 

    moment_lead_time.subtract(localzone_diff, 'hours');
    return moment_lead_time.format(format_time)
}
export const create_end_time = ( )=>{
    const coeff = 1000 * 60 * 1;
    const timezoneOffset = new Date().getTimezoneOffset() / 60;
    const t2 = new Date().setHours(new Date().getHours() - timezoneOffset * 2);
    const end_time = Math.round(t2 / coeff) * coeff
    
    return end_time

}
 


