import { DashboardTabEnum, NavigationEnum } from "types";
import { Dashborad } from "types/dashboard/Dashboard";
import { pydict } from "utils/transformation";

export const generic_click_util = (name: string, type: any, singed:boolean,  dashboard_class:Dashborad, context:any) => {
    const side = type 

    const selected: string[] = context[type];
    const selectedIndex = selected.indexOf(name);
    let newSelected: string[] = [];

    if (selectedIndex === -1) {
        newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
        newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
        newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
        newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
    }
    const obj:any = { [side]: newSelected };
    const model :DashboardTabEnum= type as DashboardTabEnum
    if((dashboard_class.lead_table as any).includes( model) && newSelected.includes(name)){
        const temp_models =  dashboard_class[model ].get_merege_model()[name]
        // let  add_selected;
        if(![NavigationEnum.ATTACKS_TABLE, NavigationEnum.HOLISTIC].includes(type)){
            
           const add_selected = pydict(temp_models ,dashboard_class.attack_table_key,[])
           
            obj[NavigationEnum.ATTACKS_TABLE] = context.attacks.concat(dashboard_class.attacks.get_which_ids_exists_in_model(add_selected));
            obj[NavigationEnum.HOLISTIC] = context.holistic.concat(dashboard_class.holistic.get_which_ids_exists_in_model(add_selected));

        }
        else{
            const devices = dashboard_class.devices.get_merege_model()
            const  add_selected = Object.keys(devices).filter((key:string)=> pydict(devices[key],dashboard_class.attack_table_key,[]).includes(name))
            
             obj['devices'] = context.devices.concat(add_selected);
            }

        
    }
    Object.keys((key:string)=>{
        obj[key] = Array.from(new Set(obj[key]))
    })
    return obj


};

 

export const generic_handleSelectAllClick_util = (checked:boolean, type: DashboardTabEnum,singed:boolean, dashboard_class:Dashborad, context:any) => {
    const obj: any = {};
    let newSelectedId: string[] = Object.keys(singed?dashboard_class[type].get_signed_objs():
    dashboard_class[type].get_objs())

    if (checked) {
            newSelectedId = newSelectedId.concat(context[type])
       
    }
    if (!checked) {
        newSelectedId = context[type].filter((key:string)=> !newSelectedId.includes(key))
    }
    obj[type] = Array.from(new Set(newSelectedId))

    return obj

};