import {  ATTACK_FIELDS, DashboardTabEnum, KeyedObject } from "types";
import { OrderData } from "views/secunity-ui/Table";
import { array_obj_to_obj, pydict } from "../transformation";

// table sort
export function descendingComparator(a: KeyedObject, b: KeyedObject, orderBy: string) {
    if(b[orderBy]===undefined ||  a[orderBy]===undefined){
        
    return 0}
    if (pydict(b[orderBy],'attack.bps',0 )< pydict(a[orderBy],'attack.bps',0)) {
        return -1;
    }
    if (pydict(b[orderBy],'attack.bps' ,0)> pydict(a[orderBy],'attack.bps',0)) {
        return 1;
    }
    if (pydict(b[orderBy],'attack.pps',0) < pydict(a[orderBy],'attack.pps',0)) {
        return -1;
    }
    if (pydict(b[orderBy],'attack.pps',0) > pydict(a[orderBy],'attack.pps',0)) {
        return 1;
    }
    if (pydict(b[orderBy],'flow.bps',0) < pydict(a[orderBy],'flow.bps',0)) {
        return -1;
    }
    if (pydict(b[orderBy],'flow.bps',0) > pydict(a[orderBy],'flow.bps',0)) {
        return 1;
    }
    if (pydict(b[orderBy],'flow.pps',0) < pydict(a[orderBy],'flow.pps',0)) {
        return -1;
    }
    if (pydict(b[orderBy],'flow.pps',0) > pydict(a[orderBy],'flow.pps',0)) {
        return 1;
    }
    // console.log();
    return b.incoming_flow.localeCompare(a.incoming_flow)
    // return 0;
}

// const getComparator: GetComparator = (order:any, orderBy:any) =>
//     order === 'desc' ? (a:any, b:any) => descendingComparator(a, b, orderBy) : (a:any, b:any) => -descendingComparator(a, b, orderBy);

export const stableSort=(array: any[]
    // comparator: (a: OrderData, b: OrderData)
     ) =>{

    const stabilizedThis = array.map((el: OrderData, index: number) => [el, index]);
    stabilizedThis.sort((a:any, b:any) => {
        const order:any = descendingComparator(a[0], b[0], 'compere')
        
        if (order !== 0) return order;
        return (a[1] as number) - (b[1] as number);
    });
    return stabilizedThis.map((el) => el[0]);
}

export const add_compere_for_sort = (flow:any, attack:any, model:DashboardTabEnum, lead_time:any):void =>{
    if (model === DashboardTabEnum.DEVICES_TABLE) {
        const attack_bps_pps_to_compere = Object.keys(flow)
            .map((key: string) => flow[key])
            // .filter((flow_o:any)=>flows_ids.includes(flow_o.flow['id']) && attacks_id.includes(flow_o.flow.incoming_flow))
            .map((attack_obj: any) => {
                return {
                    id: attack_obj.flow.id,
                    attack: {
                        bps: pydict(attack[attack_obj.flow.incoming_flow], `${ATTACK_FIELDS.STATICTICS}.${lead_time}.${ATTACK_FIELDS.BPS}`),
                        pps: pydict(attack[attack_obj.flow.incoming_flow], `${ATTACK_FIELDS.STATICTICS}.${lead_time}.${ATTACK_FIELDS.PPS}`)
                    },
                    flow: {
                        bps: pydict(attack_obj, `${ATTACK_FIELDS.STATICTICS}.${lead_time}.${ATTACK_FIELDS.BPS}`, 0),
                        pps: pydict(attack_obj, `${ATTACK_FIELDS.STATICTICS}.${lead_time}.${ATTACK_FIELDS.PPS}`, 0)
                    }
                };
            });
        Object.keys(flow).forEach((key: string) => {
            flow[key] = { ...flow[key], compere: array_obj_to_obj(attack_bps_pps_to_compere, key) };
        });
    }
    if ([DashboardTabEnum.ATTACKS_TABLE, DashboardTabEnum.NETWORKS_TABLE].includes(model)) {
        const attack_bps_pps_to_compere = Object.keys(attack)
            .map((key: string) => attack[key])
            .map((attack_obj: any) => {
                return {
                    id: attack_obj.flow.id,
                    attack: {
                        bps: pydict(attack_obj, `${ATTACK_FIELDS.STATICTICS}.${lead_time}.${ATTACK_FIELDS.BPS}`, 0),
                        pps: pydict(attack_obj, `${ATTACK_FIELDS.STATICTICS}.${lead_time}.${ATTACK_FIELDS.PPS}`, 0)
                    }
                };
            });
        // console.log(ff);
        Object.keys(attack).forEach((key: string) => {
            attack[key] = { ...attack[key], compere: array_obj_to_obj(attack_bps_pps_to_compere, key) };
        });
    }
}


