    children: [
        ...[{path:'accounts', icon:tabler_icons.IconBrowser} ,{path:'users', icon:tabler_icons.IconUsers} ,
        {path:'networks', icon:tabler_icons.IconAccessPoint}     ,{path:'devices', icon:tabler_icons.IconRouter}    
      ].map((obj:any)=>{
            return{
                id: obj.path,
                title: <FormattedMessage id={obj.path} />,
                type: 'item',
                url: `/table/${obj.path}`,
                icon: obj.icon,
                breadcrumbs: false
              }
          }),
     
 
     
               
             
            ]
