import { DashboardTabEnum,  dashboard_res, NavigationEnum } from 'types';
import { array_to_dict, pydict } from 'utils';
import { DashboradTotals } from 'utils/transformation/gauge_info';

const model_table_key = {
    [DashboardTabEnum.ATTACKS_TABLE]: NavigationEnum.DEVICES_TABLE,
    [DashboardTabEnum.DEVICES_TABLE]: NavigationEnum.DEVICES_TABLE,
    [DashboardTabEnum.NETWORKS_TABLE]: NavigationEnum.NETWORKS_TABLE
};

const model_lead_keys = {
    [DashboardTabEnum.ATTACKS_TABLE]: [NavigationEnum.ATTACKS_TABLE, NavigationEnum.HOLISTIC],
    [DashboardTabEnum.DEVICES_TABLE]: [NavigationEnum.DEVICES_TABLE],
    [DashboardTabEnum.NETWORKS_TABLE]: [NavigationEnum.NETWORKS_TABLE]
};
const attack_table_key = {
    [DashboardTabEnum.ATTACKS_TABLE]: NavigationEnum.ATTACKS_TABLE,
    [DashboardTabEnum.DEVICES_TABLE]: NavigationEnum.ATTACKS_TABLE,
    [DashboardTabEnum.NETWORKS_TABLE]: NavigationEnum.ATTACKS_TABLE
};
 
const default_value_base_model_dashboard2 = {
    page_ids: [],
    signed_ids: [],
    total: 0
};

export class BaseFlowDashborad {}
export class BaseModelDashborad {
    objs: any;
    signed_objs: any;
    total: number;
    model_objs: any;

    constructor(dashboard_obj: dashboard_res, model: NavigationEnum) {
        const [model_objs, page_info] = [pydict(dashboard_obj, model), pydict(dashboard_obj, `signed.${model}`)];
        const { page_ids, signed_ids, total } = page_info || default_value_base_model_dashboard2;
        this.objs = array_to_dict(
            page_ids.map((id: string) => {
                return { ...model_objs[id], id };
            })
        );
        this.signed_objs = array_to_dict(
            signed_ids.map((id: string) => {
                return { ...model_objs[id], id };
            })
        );
        this.total = total;
        this.model_objs = model_objs;
    }
    get_objs() {
        return this.objs;
    }
    get_signed_objs() {
        return this.signed_objs;
    }
    get_total() {
        return this.total;
    }
    get_merege_model() {
        return Array.isArray(this.objs) ? [...this.objs, ...this.signed_objs] : { ...this.objs, ...this.signed_objs };
    }
    
    add_signed(id:string) {
        if(this.objs[id])
        this.signed_objs[id] =  this.objs[id]
    }
    add_many(ids:string[]) {
        ids.forEach(id=>{
            this.add_signed(id)
        })
    }
    remove_signed(id:string) {
        delete this.signed_objs[id]
    }
    get_which_ids_exists_in_model(ids:string[]){
        const model_keys = Object.keys(this.get_merege_model())
        return ids.filter(value => model_keys.includes(value))
    }


}
export interface IBaseModelDashborad extends BaseModelDashborad {}

export class Dashborad {
    attack_table_key: NavigationEnum;
    model_table_key: NavigationEnum;
    lead_table:NavigationEnum[];
    devices:  BaseModelDashborad;
    totals: DashboradTotals;
    networks:  BaseModelDashborad;
    attacks:  BaseModelDashborad;
    flows: any;
    blackholes: BaseModelDashborad;
    holistic:  BaseModelDashborad;

    constructor(private dashboard_obj: dashboard_res, public model_name: DashboardTabEnum) {
        this.networks = new BaseModelDashborad(this.dashboard_obj, NavigationEnum.NETWORKS_TABLE);
        this.attacks = new BaseModelDashborad(this.dashboard_obj, NavigationEnum.ATTACKS_TABLE);
        this.devices = new BaseModelDashborad(this.dashboard_obj, NavigationEnum.DEVICES_TABLE);
        this.holistic = new BaseModelDashborad(this.dashboard_obj, NavigationEnum.HOLISTIC);
        this.blackholes = new BaseModelDashborad(this.dashboard_obj, NavigationEnum.BLACKHOLE);
        const all_attack = { ...this.attacks.get_merege_model(), ...this.holistic.get_merege_model() };
        this.flows = Object.assign({}, ...Object.keys(all_attack).map((key: string) => pydict(all_attack, `${key}.flow.flows`, {})));

        this.totals = new DashboradTotals(pydict(this.dashboard_obj, 'totals'));
        
        this.lead_table = model_lead_keys[this.model_name]
        this.model_table_key = model_table_key[this.model_name];
        this.attack_table_key = attack_table_key[this.model_name];
    }
    private get_model = (table_key: string): BaseModelDashborad => {
        return pydict(this, table_key);
    };
    get_objs_model() {
        return this.get_model(this.model_table_key).get_objs();
    }
    get_signed_objs_model() {
        return this.get_model(this.model_table_key).get_signed_objs();
    }

    get_total_model() {
        return this.get_model(this.model_table_key).get_total();
    }

    show_signed_model() {
        return Object.keys(this.get_signed_objs_model()).length > 0;
    }
    get_objs_attacks() {
        return this.get_model(this.attack_table_key).get_objs();
    }
    get_total_attacks() {
        return this.get_model(this.attack_table_key).get_total();
    }
    get_signed_objs_attacks() {
        return this.get_model(this.attack_table_key).get_signed_objs();
    }
    show_signed_attacks() {
        return Object.keys(this.get_signed_objs_attacks()).length > 0;
    }

    merege_model() {
        return this.get_model(this.model_table_key).get_merege_model();
    }

    merege_attacks() {
        return { ...this.attacks.get_merege_model() ,  ...this.holistic.get_merege_model() }
    }
}
