export const pydict =  (dict:object, path:string, default_ans:any=undefined)=> {
    const props = path.split('.').reverse()
    let key = props.pop() as string
    
    let ans ={...dict}[key]
    
    // ans = ans[key]
    while(props.length> 0 &&  ans!==undefined){
        key = props.pop() as string
        
        ans = ans[key]
    }
        
    return ans!==undefined &&ans!==null  ? ans : default_ans
    
};
