import { GridSize } from "@mui/material";
import { capitalize_array } from "utils";
import { DropListTypes, FormField ,} from "..";


export const replaceAt= (str:string, index:number , char: string) => {
  const a = str.split("");
  a[index] = char;
  return a.join("");
}
export enum TypeForm {
  PASSWORD = 'password',
  TEXT = 'text',
  NUMBER = "number",
  DROPLIST = 'droplist',
  MULTI_DROPLIST = 'multi_droplist',
  OBJECT = 'object',
  RADIO = 'radio',
  CHIP = 'chip',
  OPEN = 'open',

}
export interface GridStyle {
  lg: GridSize;
  xs: GridSize;
  md: GridSize;
  mt?: GridSize;
  ml?: GridSize;

}

export class FormControlProps {

  constructor(
    readonly type1: string,
    readonly placeholder: string,
    readonly type: TypeForm = TypeForm.TEXT,
    public identifier: string='key_def',
    readonly autoComplete?: string,
    public value_cur: string='',
    readonly captionLabel?: string,
    // readonly inputChangedHandler?:any

  ) {
    this.captionLabel = capitalize_array(captionLabel ? captionLabel.split('_') : placeholder.split('_'))
  }
}
export type StashObj = {
  [key: string]: any
  // min: '11',
  // max: '11',
  // avg: '11',
  // recommend: '11',
}
export class FormControlPropsThreshold {

  constructor(
    readonly type1: string,

    readonly placeholder: string,
    public identifier: string='key_def',
    readonly type: TypeForm = TypeForm.NUMBER,
    public value_cur: string='',
    public recommend_values?: StashObj,
    readonly captionLabel?: string,
   
  ) {
    this.captionLabel = capitalize_array(captionLabel ? captionLabel.split('_') : placeholder.split('.'))

  }
}


export class FormControlPropsRadioButtonWithInput {

  constructor(
    readonly type1: string,

    readonly placeholder: string,
    readonly options: string[],
    public identifier: string='key_def',
    readonly input_field: FormControlProps,
    public value_cur: string='',
    public values: any[]=[],

    readonly type: TypeForm = TypeForm.RADIO,

  ) {
    this.values = [{ value_cur: options[0]},{ value_cur: input_field.value_cur}]
  }
}
export class FormControlPropsSwitchAnimate {

  constructor(
    readonly type1: string,

    readonly placeholder: string,
    public identifier: string='key_def',
    readonly input_field: FormControlProps,
    public value_cur: string='',
    public values: any[]=[],

    readonly type: TypeForm = TypeForm.RADIO,

  ) {
    this.values = [{ value_cur},{ value_cur: input_field.value_cur}]
  }
}
