export interface SelectField {
  captionLabel: string;
  type_selects: DropListTypes;
  default_value: number;

}
export interface CheckBoxField {
  checked: boolean;

}
export interface InputField {
  settings: FormField
  grid_style?: GridStyle
};

export interface InputField2 {
  settings: FormControlProps | SelectField;
  grid_style: GridStyle
};

export type FieldsType = InputField | SelectField | undefined;
export type FormOld = {
  [type: string]: InputField;
};

