// material-ui
import { useTheme } from '@mui/material/styles';
import { Avatar, Box, ButtonBase ,Typography,
    Grid
} from '@mui/material';

// project imports
import LogoSection from '../LogoSection';
// import SearchSection from './SearchSection';
import MobileSection from './MobileSection';
import ProfileSection from './ProfileSection';
// import NotificationSection from './NotificationSection';

// assets
import { IconMenu2 } from '@tabler/icons';
import useAuth from 'common/hooks/useAuth';
import { ACCOUNT_TYPE } from 'types';
import HocNavigate from 'common/hooks/useNavigate';

// ==============================|| MAIN NAVBAR / HEADER ||============================== //

export interface HeaderProps {
    handleLeftDrawerToggle: () => void;
}

const Header = ({ handleLeftDrawerToggle }: HeaderProps) => {
    const theme = useTheme();
    const { permissions } = useAuth();

    const username =permissions.name
    const role = permissions.type_account?permissions.type_account:ACCOUNT_TYPE.ISP
    const is_view_as = HocNavigate().is_view_as()
    // const role = typeof username==='string' && ['OSHI1','TAMARES'].includes(username?.toLocaleUpperCase())?'ISP': 'CUSTOMER'
    if (is_view_as && username)
    document.title = username;

    return (
        <>
            {/* logo & toggler button */}
            <Box
                sx={{
                    width: 228,
                    display: 'flex',
                    [theme.breakpoints.down('md')]: {
                        width: 'auto'
                    }
                }}
            >
                <Box component="span" sx={{ display: { xs: 'none', md: 'block' }, flexGrow: 1 }}>
                    <LogoSection />
                </Box>
                <ButtonBase sx={{ borderRadius: '12px', overflow: 'hidden' }}>
                    <Avatar
                        variant="rounded"
                        sx={{
                            ...theme.typography.commonAvatar,
                            ...theme.typography.mediumAvatar,
                            transition: 'all .2s ease-in-out',
                            background: theme.palette.mode === 'dark' ? theme.palette.dark.main : theme.palette.secondary.light,
                            color: theme.palette.mode === 'dark' ? theme.palette.secondary.main : theme.palette.secondary.dark,
                            '&:hover': {
                                background: theme.palette.mode === 'dark' ? theme.palette.secondary.main : theme.palette.secondary.dark,
                                color: theme.palette.mode === 'dark' ? theme.palette.secondary.light : theme.palette.secondary.light
                            }
                        }}
                        onClick={handleLeftDrawerToggle}
                        color="inherit"
                    >
                        <IconMenu2 stroke={1.5} size="1.3rem" />
                    </Avatar>
                </ButtonBase>

            </Box>

            {/* header search */}
            {/* <SearchSection /> */}

            {/* <Box sx={{ flexGrow: 1 }} />
            <Chip label='view as ISP' color='primary'/>
            <Chip label='Your account Gilat  your user Dudu' color='primary'/> */}
            {/* <Box  /> */}
            <Box sx={{ flexGrow: 1 }} />

{is_view_as &&  <Typography textAlign='center' variant="h3" mb={1} color='primary'>view as mode</Typography>}
            <Grid sx={{ flexGrow: 1 , displey:'flex', direction:'column'}}>

 <Typography textAlign='center' variant="h4" mb={1}>{username}</Typography>

            <Typography textAlign='center' variant="subtitle2">{role}</Typography>
            </Grid>
 

            {/* notification & profile */}
            {/* <NotificationSection /> */}
            <Box sx={{ flexGrow: 1 }} />
            <ProfileSection />
            
            {/* mobile header */}
            <Box sx={{ display: { xs: 'block', sm: 'none' } }}>
                <MobileSection />
            </Box>
        </>
    );
};

export default Header;
