// action - state management
import { LOGIN, LOGOUT, SET_PERMISSIONS } from '../state/type_actions';
import { initialLoginContextProps } from 'types';

// ==============================|| ACCOUNT REDUCER ||============================== //

const initialState: initialLoginContextProps = {
    isLoggedIn: false,
    permissions:{}

};

export interface AccountReducerActionProps {
    type: string;
    payload?: initialLoginContextProps;
}

const accountReducer = (state = initialState, action: AccountReducerActionProps) => {
    switch (action.type) {
  
        case LOGIN: {
            
            return {
                ...state,
                isLoggedIn: true,
                isInitialized: true,
            };
        }
        case LOGOUT: {
            return {
                ...state,
                isInitialized: true,
                isLoggedIn: false,
                user: null,

            };
        }
        case SET_PERMISSIONS: {
            return {
                ...state,
    permissions:action.payload?.permissions
                

            };
        }
        default: {
            return { ...state };
        }
    }
};

export default accountReducer;
