import { Dispatch } from "redux";
import config from 'config';
import {  useParams, useNavigate } from 'react-router-dom';
import { objApi, pydict } from 'utils';
import React from 'react';
import { BaseModelDashborad } from 'types/dashboard/Dashboard';
import { useDispatch } from 'react-redux';

const get_body = (newPage:number,   model:string, params:any,context:any) => {
     context.dashboard_class &&console.log(Object.keys(context.dashboard_class));
     
     const page_limit = config.page_dash
     const pages = (params.page ? params.page.split('-') : [1, 1]).map((num:any)=>parseInt(num,10));
     const model_name = params.model_name
     const is_left = model_name=== model || model.includes(model_name as string)
     
     if(newPage>-1){
     if(is_left){
     pages[0]=newPage;
     pages[1]=1;
 }
     else pages[1]=newPage;

}
     // console.log(pages,model_name=== model,  model_name as string, model);
 
 
     const body = Object.assign({}, 
        ...(['networks', 'attacks', 'devices', 'holistic']).
     //     ...Object.keys(context.dashboard_class ||{}).
         map((key:string)=>{
          const a:BaseModelDashborad= pydict(context ,`dashboard_class.${key}`, false);
               
               return {[key]: {
                    signed_ids:a?Object.keys(a.get_signed_objs()):[],
                    page_offset:   pages[model_name===key?0:1],
                    page_limit
               }}
          }) 
          )
   
     return [body, pages.join('-')];
 };
const  HocNavigate =  (  ) => {
  const params = useParams();
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const request_navigate =  (url:any) => {
      
       navigate(`${get_prefix_to_navigate()}${url}`)
 
  };
  const fetch_model_by_id = React.useCallback(async (dispatch1:Dispatch) => {
     // const body = {"options": [ "extended", 'threshold']}
     const body = {"options": [ "extended"]}
     body['options'] = ['general', 'extended', 'system', 'threshold','rate_limit_ports', 'recommendations', 'traffic_baseline', 'detailed_recommendation','flatten_rate_limit_bps']
const user_id = localStorage.getItem('user_account_id');

     const prefix = `/${params.account_name? params.account_name.toString():user_id}`

     const ans =  await objApi.post(`${prefix}/${params.model_name}/${params.id}`, body, dispatch1);
    return ans
  
   }, [ ]) 
 
  const get_report = React.useCallback(async ( ) => {
     // const body = {"options": [ "extended", 'threshold']}
     
const user_id = localStorage.getItem('user_account_id');

     const prefix = `/${params.account_name? params.account_name.toString():user_id}`

     const ans =  await objApi.get(`${prefix}/reports/monthly`, dispatch);



   
    return ans
  
   }, [ ]) 
 
 
  const get_prefix_to_navigate =  ( ) => {
      const pre = params.account_name? `/v/${params.account_name.toString()}/`:'/'
       return pre
  };
  const is_view_as =  ( ) => params.account_name!==undefined

  const get_body_dashboard =  (newPage:number,    model:string, context:any) => {
     const [body, pages]=  get_body(newPage,   model, params ,context)
     return [body, pages]

  }
  

  return {is_view_as,get_prefix_to_navigate,
     request_navigate,get_body_dashboard, params, fetch_model_by_id,get_report
  };
};
export default  HocNavigate