const change_index_identifier = (index:number, key:string, obj:any):string => (
    obj[key].length > 1 ? `${index.toString()}${obj[key].slice(1)}` : index.toString()

)
export const insert = (arr: any[], index: number, newItem: any) => {
    const ans = [...arr.slice(0, index), newItem, ...arr.slice(index)];
    
    ans[index].identifier = index.toString();
    while (++index <= arr.length)
      if( ans[index].identifier!==undefined)
        ans[index].identifier =change_index_identifier(index, 'identifier', ans[index])
            // ans[index].identifier.length > 1 ? `${index.toString()}${ans[index].identifier.slice(1)}` : index.toString();
        else if( ans[index].key!==undefined){
        ans[index].key =change_index_identifier(index, 'key', ans[index])
            if(ans[index].form_elements ){
            const index_spe = index
                const new_ans_index = ans[index_spe].form_elements.map((element:any) => {
                    element.identifier = change_index_identifier(index_spe, 'identifier',element)
                    return element
                });
            ans[index].form_elements = new_ans_index

            }
    }
    return ans;
};

export const  object_equals = ( x:any, y:any )=> {
    if ( x === y ) return true;
      // if both x and y are null or undefined and exactly the same
  
    if ( ! ( x instanceof Object ) || ! ( y instanceof Object ) ) return false;
      // if they are not strictly equal, they both need to be Objects
  
    if ( x.constructor !== y.constructor ) return false;
      // they must have the exact same prototype chain, the closest we can do is
      // test there constructor.
  
    for ( const p in x ) {
      if ( ! x.hasOwnProperty( p ) ) continue;
        // other properties were tested using x.constructor === y.constructor
  
      if ( ! y.hasOwnProperty( p ) ) return false;
        // allows to compare x[ p ] and y[ p ] when set to undefined
  
      if ( x[ p ] === y[ p ] ) continue;
        // if they have the same strict value or identity then they are equal
  
      if ( typeof( x[ p ] ) !== "object" ) return false;
        // Numbers, Strings, Functions, Booleans must be strictly equal
  
      if ( ! object_equals( x[ p ],  y[ p ] ) ) return false;
        // Objects and Arrays must be tested recursively
    }
  
    for (const p in y )
      if ( y.hasOwnProperty( p ) && ! x.hasOwnProperty( p ) )
        return false;
          // allows x[ p ] to be set to undefined
  
    return true;
  }