export const array_obj_to_obj = (itearble: any[], value: any): any =>Array.isArray(itearble)?  itearble.find((o:any) => o['id'] === value):itearble[value]
export const array_obj_to_index_obj = (itearble: any[], value: any): any => itearble.findIndex((o:any) => o['id'] === value)
export const filter_array_obj = (itearble: any[], value: any): any[] => itearble.filter((o:any) => o['id'] !== value)


export const array_to_index_with_key = (itearble: any[], value: any): any => itearble.findIndex((o:any) => o === value)

export const array_to_index_with_key2 = (itearble: any[], value: any, key:string): any => itearble.findIndex((o:any) => o[key] === value)

export const array_obj_to_obj_with_key = (itearble: any[], value: any, key:string): any => itearble.find((o:any) => o[key] === value)
