// material-ui
import { Typography } from '@mui/material';

// project imports
import NavGroup from './NavGroup';
import menuItem from 'common/menu-items';
import useAuth from 'common/hooks/useAuth';
import { useParams } from 'react-router-dom';

// ==============================|| SIDEBAR MENU LIST ||============================== //

const MenuList = () => {
    const { permissions } = useAuth();
    const params = useParams()
    const pre = params.account_name? `/v/${params.account_name.toString()}`:false

    const filterd_items = menuItem.items.map((item: any) => {
        let children = item.children.filter(
            (item2: any) =>
                (item2.id !== 'private_flow' || permissions.can_add_private_flow) &&
                (item2.id !== 'blackhole' || permissions.can_show_blackhole)
        );
        if(pre){
            children = children.map(
                (item2: any) =>{
                    
                    return {...item2,url: `${pre}${item2.url}` }
                }
            );
        }
        return { ...item, children };
    });

    const navItems = filterd_items.map((item) => {
        switch (item.type) {
            case 'group':
                return <NavGroup key={item.id} item={item} />;
            default:
                return (
                    <Typography key={item.id} variant="h6" color="error" align="center">
                        Menu Items Error
                    </Typography>
                );
        }
    });

    return <>{navItems}</>;
};

export default MenuList;
