export enum DashboardTabEnum {
    DEVICES_TABLE = 'devices',
    NETWORKS_TABLE = 'networks',
    ATTACKS_TABLE = 'attacks',
}
export enum DashboardModelEnum {
    FLOWS_TABLE = 'flows',
    DEVICES_TABLE = 'devices',
    NETWORKS_TABLE = 'networks',
    ATTACKS_TABLE = 'attacks',
}
export type dashboard_model_res ={objs:object|any[], signed_objs:object|any[], total:number}
export type dashboard_res2 ={
    [key in DashboardModelEnum]: dashboard_model_res;
 }
export interface dashboard_res extends dashboard_res2{
    totals:any;
}

export interface DashboardStateProps {
    attacks: dashboard_res;
    devices: dashboard_res;
    networks: dashboard_res;
}
export interface ModelsStateProps {
    accounts: any;
    users: any;
    devices: any;
    networks: any;
    flows: any;
    attacks: any;
}

export interface ModelPayloadStateProps {
    id?: string  ;
    type: string;
    accounts?:any;
    signed_key?:DashboardTabEnum;
    model?:   DashboardTabEnum;

    users?: any;
    devices?: any;
    networks?: any;
    type_form?:   'accounts' | 'networks' | 'users' | 'devices'|'flows'|'attacks';
    obj?:any;
}



