import { lazy } from 'react';

// project imports
import GuestGuard from 'common/route-guard/GuestGuard';
import MinimalLayout from 'common/layout/MinimalLayout';
import NavMotion from 'common/layout/NavMotion';
import Loadable from 'ui-component/pages/Loadable';
import { NavigationEnum } from 'types';

// login routing
const AuthLogin = Loadable(lazy(() => import('views/secunity-ui/authentication/Login')));

const LoginRoutes = {
    path: '/',
    element: (
        <NavMotion>
            <GuestGuard>
                <MinimalLayout />
            </GuestGuard>
        </NavMotion>
    ),
    children: [
        {
            path: `/${NavigationEnum.LOGIN}/:id`,
            element: <AuthLogin />
        },
        {
            path: `/${NavigationEnum.LOGIN}`,
            element: <AuthLogin />
        },
        // {
        //     path: '*',
        //     element: <AuthLogin />
        // },

    ]
};

export default LoginRoutes;
