// action - state management
import * as actionTypes from '../state/type_actions';
import { DefaultRootStateProps , ModelPayloadStateProps} from 'types';

import { Chance } from 'chance';
import { array_obj_to_index_obj, filter_array_obj } from 'utils';

const chance = new Chance();
// newProduct = { ...payload.product!, itemId: chance.timestamp() };

export const initialState: DefaultRootStateProps['models'] = {
    users: [],
    accounts: [],
    devices: [],
    networks: [],
    flows: [],
    attacks: [],

};
// import { filter } from 'lodash';
// 
// inCartProduct = filter(state.checkout.products, {
//     id: newProduct.id,
//     color: newProduct.color,
//     size: newProduct.size
// });

const modelReducer = (state = initialState, payload: ModelPayloadStateProps) => {
    let tableData
    let obj
    switch (payload.type) {
       
        case actionTypes.ADD_MODEL:
            
            tableData =[...state[payload.type_form!]]
            obj = payload.obj!
            obj['_id'] =  chance.timestamp()
            tableData.push(obj)
            return {
                ...state,
                [payload.type_form!]: tableData
            };
        case actionTypes.DELETE_MODEL:
            tableData =filter_array_obj([...state[payload.type_form!]], payload.id!)
            return {
                ...state,
                [payload.type_form!]: tableData
            };
        case actionTypes.EDIT_MODEL:

             tableData =[...state[payload.type_form!]]
             obj = payload.obj!
             const index =array_obj_to_index_obj(tableData,  payload.id!)
            
             tableData[index] = obj
            return {
                ...state,
                [payload.type_form!]: tableData
            };
        case actionTypes.SET_MODELS:
            const model = Object.keys(payload)[1]
       
            return {
                ...state,
                [model]:(payload as any)[model]

            };

       

        
        case actionTypes.RESET_DASHBOARD:
            return initialState;
        default:
            return state;
    }
};

export default modelReducer;
