// action - state management
import * as actionTypes from '../state/type_actions';
import { dashboard_res, DefaultRootStateProps, ModelPayloadStateProps,  DashboardModelEnum , DashboardTabEnum} from 'types';
import {  pydict } from 'utils';
import { Dashborad } from 'types/dashboard/Dashboard';


 export const initialDashboard: dashboard_res={
   [DashboardModelEnum.NETWORKS_TABLE]:{objs:[], signed_objs:[], total:0},
   [DashboardModelEnum.DEVICES_TABLE]:{objs:[], signed_objs:[], total:0},
   [DashboardModelEnum.ATTACKS_TABLE]:{objs:{}, signed_objs:{}, total:0},
   [DashboardModelEnum.FLOWS_TABLE]:{objs:{}, signed_objs:{}, total:0},
  totals:{}
 }

export const initialState: DefaultRootStateProps['dashboard'] = {
    attacks: {...initialDashboard},
    devices: {...initialDashboard},
    networks: {...initialDashboard},

};
