export const  return_func_dashboardReducer = (obj:any, type_form:any) =>{
    const dashboard_class =new Dashborad(obj[type_form], type_form! as DashboardTabEnum)
    obj[type_form]['dashboard_class'] = dashboard_class
    
    return obj
}

const dashboardReducer = (state = initialState, payload: ModelPayloadStateProps) => {
    // let singed_list:any;
    const {obj, type_form, model} = payload
   
    switch (payload.type) {
       
      
        case actionTypes.SET_DASHBOARD:
            // const dashboard_class =new Dashborad(obj, type_form! as DashboardTabEnum)
            // obj['dashboard_class'] = dashboard_class
            return return_func_dashboardReducer({
                ...state,
                [type_form!]:obj

            }, type_form);

            case actionTypes.ADD_SIGNED1:
                const asinged_list :Dashborad= pydict(state[model!],`dashboard_class`)
                asinged_list[payload.signed_key!].add_signed(obj['id'])
                return {
                    ...state,
                    [model!]: { ...state[model!], 'dashboard_class': asinged_list}
                }
                // singed_list = pydict(state[model!],`${payload.signed_key!}.signed_objs`)
            //     if(!Array.isArray(singed_list)){
            //         singed_list = {...singed_list}
    
            //         singed_list[obj['id']] = obj
            //     }
            //     else{
            //     singed_list = [...singed_list]
    
            //     singed_list.push(obj)
            //    } 
        
            //    return return_func_dashboardReducer({
            //         ...state,
            //         [model!]: { ...state[model!], [payload.signed_key!]:{...pydict(state[model!],`${payload.signed_key!}`),
            //         signed_objs:singed_list }}
            //     }, model);
    
            case actionTypes.REMOVE_SIGNED1:
                const asinged_list2 :Dashborad= pydict(state[model!],`dashboard_class`)
                asinged_list2[payload.signed_key!].remove_signed(payload.id!)
                return {
                    ...state,
                    [model!]: { ...state[model!], 'dashboard_class': asinged_list2}
                }
                // singed_list = pydict(state[model!],`${payload.signed_key!}.signed_objs`)


    
                // if(!Array.isArray(singed_list)){
                // singed_list = {...singed_list}
    
                // delete singed_list[payload.id!] }
                // else
                // {
                //     singed_list = [...singed_list]
    
                //     const index_to_remove = array_obj_to_index_obj(singed_list, payload.id)
                //     singed_list.splice(index_to_remove, 1);
                // }
 
                // return return_func_dashboardReducer({
                //     ...state,
                //     [model!]: { ...state[model!], [payload.signed_key!]:{...pydict(state[model!],`${payload.signed_key!}`),
                //     signed_objs:singed_list }}
                // }, model);
        case actionTypes.ADD_MANY_SIGNED1:
            const singed_list2 :Dashborad= pydict(state[model!],`dashboard_class`)
            singed_list2[payload.signed_key!].add_many(obj)

           
            return {
                ...state,
                [model!]: { ...state[model!], 'dashboard_class': singed_list2}
            }
            // singed_list ={ ...pydict(state[model!],`${payload.signed_key!}.signed_objs`),  ...obj}
            
            // return return_func_dashboardReducer({
            //     ...state,
            //     [model!]: { ...state[model!], [payload.signed_key!]:{...pydict(state[model!],`${payload.signed_key!}`),
            //     signed_objs:singed_list }}
            // }, model);
        
        case actionTypes.RESET_DASHBOARD:
            return initialState;
        default:
            return state;
    }
};

export default dashboardReducer;
