// action - state management
import * as actionTypes from '../state/type_actions';
import { ContextPayloadStateProps, DefaultRootStateProps } from 'types';
import { array_obj_to_index_obj } from 'utils';

export const initialStateContext2 = { attacks_obj: {}, devices_obj: [], networks_obj: [], flows_obj: {}, 
attacks_signed: {}, devices_signed: [], networks_signed: [], flows_signed: {} };
const initialStateContext = { left: [], right: [], attacks: [], networks: [], holistic: [], devices: []};
export const initialState: DefaultRootStateProps['context'] = {
    attacks: initialStateContext,
    devices: initialStateContext,
    networks: initialStateContext,
    ratelimit: []
};

const contextReducer = (state = initialState, payload: ContextPayloadStateProps) => {
    const { obj, model , } = payload;
    let singed_list:any;

    switch (payload.type) {
        case actionTypes.SET_SELECTED:
            Object.keys(obj).forEach((side) => {
            obj[side] = Array.from(new Set( obj[side]))
            })
 
            return {
                ...state,
                [model!]: { ...state[model!], ...obj }
            };
        case actionTypes.ADD_MANY_SIGNED:
            
            singed_list = {...state[model!][payload.signed_key!], ...obj}

            return {
                ...state,
                [model!]: { ...state[model!], [payload.signed_key!]:singed_list}
            };
        case actionTypes.ADD_SIGNED:
            
            // singed_list = [...state[model!][payload.signed_key!]]
            singed_list = state[model!][payload.signed_key!]
            if(!Array.isArray(singed_list)){
                singed_list = {...singed_list}

                singed_list[obj['id']] = obj
            }
            else{
            singed_list = [...singed_list]

            singed_list.push(obj)
           } 
           
           return {
                ...state,
                [model!]: { ...state[model!], [payload.signed_key!]:singed_list}
            };

        case actionTypes.REMOVE_SIGNED:
            singed_list = state[model!][payload.signed_key!]

            if(!Array.isArray(singed_list)){
            singed_list = {...singed_list}

            delete singed_list[payload.id!] }
            else
            {
                singed_list = [...singed_list]

                const index_to_remove = array_obj_to_index_obj(singed_list, payload.id)
                singed_list.splice(index_to_remove, 1);
            }
            return {
                ...state,
                [model!]: { ...state[model!], [payload.signed_key!]:singed_list}

            };

        case actionTypes.SET_RATELIMIT:
            return {
                ...state,
                ratelimit: payload.obj
            };

        case actionTypes.SET_RATELIMIT2:
            const ratelimit = [...state.ratelimit];
            ratelimit.splice(obj, 1);
            return {
                ...state,
                ratelimit
            };

        case actionTypes.RESET_CONTEXT:
            return initialState;
        default:
            return state;
    }
};

export default contextReducer;
